import categorias from "@/assets/moveis-db/categorias.json";

import adegas from "@/assets/moveis-db/moveis/1.json";
import aparadores from "@/assets/moveis-db/moveis/2.json";
import armarios from "@/assets/moveis-db/moveis/3.json";
import bancos from "@/assets/moveis-db/moveis/4.json";
import sofas from "@/assets/moveis-db/moveis/5.json";
import mesas from "@/assets/moveis-db/moveis/6.json";
import outros from "@/assets/moveis-db/moveis/7.json";
import axios from "axios";

const categorias_produtos = [
  adegas,
  aparadores,
  armarios,
  bancos,
  sofas,
  mesas,
  outros,
];
export interface Common {
  id: number;
  nome: string;
  url: string;
  img: string;
}
export class Categoria implements Common {
  id: number;
  nome: string;
  img: string;
  url: string;

  private constructor(id: number, name: string, icon: string) {
    this.id = id;
    this.nome = name;
    this.img = icon;
    this.url = `/categorias/${this.id}/produtos`;
  }

  static getCategorias(): Categoria[] {
    const result = categorias.map((x) => new Categoria(x.id, x.nome, x.img));
    return result;
  }

  static getCategoria(id: number): Categoria | null {
    const categoria = categorias.find((x) => x.id == id);
    if (!categoria) {
      return null;
    }
    return new Categoria(categoria.id, categoria.nome, categoria.img);
  }
}
export class Movel implements Common {
  id: number;
  nome: string;
  imgs: string[];
  descricao: string;
  url: string;
  img: string;

  private constructor(
    id: number,
    nome: string,
    imgs: string[],
    descricao: string,
    categoria: Categoria
  ) {
    this.id = id;
    this.nome = nome;
    this.imgs = imgs;
    this.descricao = descricao;
    this.url = `/categorias/${categoria.id}/produtos/${id}`;
    this.img = imgs[0];
  }

  static getMoveis(categoria: Categoria): Movel[] {
    const categoria_moveis = categorias_produtos[categoria.id - 1].map(
      (x) => new Movel(x.id, x.nome, x.imgs, x.descricao, categoria)
    );

    return categoria_moveis;
  }

  static getMovel(categoria: Categoria | null, movelId: number): Movel | null {
    if (!categoria) return null;
    const allmoveis = this.getMoveis(categoria);
    const movel = allmoveis.find((x) => x.id == movelId);
    if (!movel) return null;
    return movel;
  }
}

const baseAxios = axios.create({
  baseURL: "https://api.madeiranobremoveis.com.br/",
  headers: {
    "Content-Type": "application/json",
  },
});

export interface SimplifiedUsuario {
  id: number;
  username: string;
}
export class Login {
  token: string;
  user: SimplifiedUsuario;

  private constructor({ token, user }: Login) {
    this.token = token;
    this.user = user;
  }

  static async logar(
    username: string,
    password: string
  ): Promise<Login | null> {
    try {
      const response = await baseAxios.post("auth", {
        username,
        password,
      });
      return new Login(response.data);
    } catch {
      return null;
    }
  }
}

export enum ErrorLevel {
  NoDefined = 0,
  Severe,
  Error,
  Warning,
  Info,
  Verbose,
}
export class ErrorViolet {
  private severity: ErrorLevel;
  private message: string;

  private constructor(severity: ErrorLevel, message: string) {
    this.message = message;
    this.severity = severity;
  }

  private send() {
    baseAxios
      .post("/errors", this)
      .then(() =>
        console.log(
          `[${Date.now().toString()} {${this.severity.toString()}}] ${
            this.message
          }`
        )
      )
      .catch(console.error);
  }

  static throw({ message }: Error): void {
    ErrorViolet.error(message);
  }

  static error(message: string): void {
    const err = new ErrorViolet(ErrorLevel.Error, message);
    err.send();
  }

  static warning(message: string): void {
    const err = new ErrorViolet(ErrorLevel.Warning, message);
    err.send();
  }

  static severe(message: string): void {
    new ErrorViolet(ErrorLevel.Severe, message).send();
  }

  static info(message: string): void {
    new ErrorViolet(ErrorLevel.Info, message).send();
  }

  static verbose(message: string): void {
    new ErrorViolet(ErrorLevel.Verbose, message).send();
  }
}
